import React, { Component } from 'react';
import { Link } from "react-router-dom";
import './style.scss';

class MenuItem extends Component{
  render(){
    const { name, linkstring, inuse } = this.props;
    return(
      <div>
      {
        inuse?
        <div className="menu-item">
          <Link to={linkstring}>
            <div className="cover-box">{name}</div>
            <div className="hover-box">{name}</div>
          </Link>
        </div>
        :
        <div className="menu-item">
          <div className="cover-box">{name}</div>
          <div className="hover-box">{name}</div>
        </div>
      }
  </div>
    )
  }
}

export default class MainMenu extends Component{
  render(){
    //读取当前link，进行变色
    return(
      <div className="menu">
        <MenuItem name="&lt;Home/>" linkstring="/"/>
        <MenuItem name="&lt;Gallery/>" linkstring="/gallery" inuse={false} />
        <MenuItem name="&lt;Project/>" linkstring="/project"inuse={false} />
        {/* <MenuItem name="&lt;Blog/>" linkstring="/blog"inuse={false} /> */}
      </div>
    )
  }
}


