import React, { Component } from 'react';
import WordCloud from 'wordcloud';
import './style.css';
import mobiles from '../../Images/mobiles.png';
import pic1 from '../../Images/leapstack/1.png';
import pic2 from '../../Images/leapstack/2.png';
import pic3 from '../../Images/leapstack/3.png';
import pic4 from '../../Images/leapstack/4.png';
import pic5 from '../../Images/leapstack/5.png';
import pic6 from '../../Images/leapstack/6.png';
import pic7 from '../../Images/leapstack/7.png';
// import _ from 'lodash';

export default class Home extends Component{

  state = {
    height: window.innerHeight,
    pjoffset: 0,
    lineidx: 0,
    page:0,
    scrollTop: window.scrollY
  }

  componentDidMount() {
    window.addEventListener('wheel', (e) => {
      e.preventDefault();
      let { scrollTop, page, height, pjoffset} = this.state;
      // console.log(scrollTop);
      if(scrollTop>2.8*height){
        page = 3;
      }else if(scrollTop>1.8*height){
        page = 2;
      }else if(scrollTop>height){
        page = 1;
      }else if(scrollTop<height){
        page = 0;
      }
      if(event.deltaY>0&&scrollTop<2*height){
        if(scrollTop>1.8*height&&pjoffset<=1600){
          this.setState({pjoffset: pjoffset+70});
          console.log(pjoffset);
        }else
          this.setState({scrollTop: scrollTop+70, page});
      }else if(event.deltaY<0&&scrollTop>0){
        if(scrollTop<1.8*height&&pjoffset>0){
          this.setState({pjoffset: pjoffset-70});
          console.log(pjoffset);
        }else
        this.setState({scrollTop: scrollTop-70, page});
      }
      // console.log(page);
    },
    {passive:false});
    this.setPassions();
    // this.drawWordCloud();
  }


  setPassions = () => {
    let _this = this;
    setInterval(function(){ 
       _this.setState(function(prev){
         let current = (prev.lineidx + 1) % 4;
         return {lineidx: current};
        });
    },2000);
  }

  drawWordCloud = () => {
    var el = this.refs['wordcloud'];
    var newCanvas = el.firstChild;
    newCanvas.id = 'wordcloud';
    newCanvas.height = 400;
    newCanvas.width = 500;
    WordCloud(el.firstChild, { 
      list: [
        ["Java",100],
        ["JavaScript",100],
        ["React",80],
        ["SQL",80],
        ["HTML/CSS",80],
        ["Python",80],
        ["MySQL",60],
        ["AntD",60],
        ["D3",60],
        ["SASS/Less",60],
        ["Git",60],
        ["Oracle",60],
        ["MongoDB",40],
        ["Npm/yarn",40],
        ["Node.js",40],
        ["Webpack",40],
        ["C",60],
        ["SpringBoot",40],
        ["AngularJS",40],
        ["ABAP",40],
      ],
      color: function() {
        return ['rgba(84,148,156,1)', 'rgba(128,204,206,1)', 'rgba(192,223,205,1)'][Math.floor(Math.random() * 3)];
        // return (['#d87c7c','#919e8b', '#6e7074','#61a0a8','#efa18d', '#787464', '#E7E34E', '#724e58', '#4b565b','#DA4453','#8CC152','#967ADC','#D770AD','#3BAFDA','#EF7E32'])[Math.floor(Math.random() * 15)]
      },
      rotateRatio: 0,
      backgroundColor: 'transparent'
    } );
  }

  render(){
    const { lineidx, scrollTop, page, height, pjoffset } = this.state;
    const scalei = (scrollTop-height*2/3)*0.02;
    const lines = ["turning ideas into reality", "learning new things", "capturing the beauty of everyday life", "writing my own stories"];
    return(
      <div className="norm">
        <div className="screen1" style={{height: height, 
          transform: `translateY(${page===0&&-scrollTop}px)`
          }}>
          <div className="intro" style={{transform: `translateY(${scrollTop*0.3}px)`}}>
            <div className="nameline">Hi, my name is <div className="name">May Yang</div></div>
            <div className="titleline">I can do 
              <div>Develop</div>
              <div>/</div>
              <div>Design</div>
              <div>/</div>
              <div>Photograph</div>
            </div>
            <div className="passionline">
              <div>My passion is</div>
              <div className="glitchline"><div title={lines[lineidx]}>{lines[lineidx]}</div></div>
            </div>
          </div>
          <div className="motto motto-line1"  style={{transform: `translateX(${scrollTop*0.3}px)`}}>TILL GOOD IS</div>
          <div className="motto motto-line2"  style={{transform: `translateX(${scrollTop*0.1}px)`}}>BETTER, AND</div>
          <div className="motto motto-line3"  style={{transform: `translateX(${-scrollTop*0.9}px)`}}>BETTER IS BEST</div>
          {/* <img className="downarrow" src={} alt="" /> */}
        </div>
        <div className="screen2" style={{height: height, top: height,
        transform: `translateY(${(page===0||page===1)&&-scrollTop}px)`
        }}>
          {/* <div ref="wordcloud" className="wordcloud">
            <canvas/>
          </div>
          <div className="experience">
            <div>- Scrum agile methodology</div>
            <div>- Full-stack development experience</div>
            <div>- Worked in rapid-growth startup</div>
          </div> */}
          <div className="drama">
            <div className="texts" style={{transform: `translateY(${-scrollTop*0.2}px)`}}>
              <div className="subtitle">Wireframe Design</div>
              <div className="text-title" style={{transform: `scaleY(${scalei<0.9?0.9:scalei>1.1?1.1:scalei})`}}>Dramalife </div>
              <div className="text-desc">
                <div>An app for drama, musical and opera fans:</div>
                <div> • &nbsp;&nbsp;Discover new events and activities</div>
                <div> • &nbsp;&nbsp;Generate your own daily schedule</div>
                <div> • &nbsp;&nbsp;Share your score and comment on musicals</div>
              </div>
              <img className="drama-pic" src={mobiles} alt='' />
            </div>
          </div>
        </div>
        <div className="screen3"  style={{height: height*0.8, top: 2*height,
        transform: `translateY(${(page===1||page===2)&&-scrollTop}px)`}}>
          <div className="subtitle" style={{transform: `translateY(${-(scrollTop-height)*0.2}px)`}}>Selected projects</div>
          <div className="pj" style={{transform: `translateY(${-(scrollTop-height)*0.2}px)`}}>
            <div className={`pj-title ${pjoffset<600&&scrollTop>1.6*height?'appear':''} ${pjoffset>=600?'fade':''}`}>Responsive Website</div>
            <div className={`pj-title ${pjoffset<1000&&pjoffset>600?'appear':''} ${pjoffset>=1000||pjoffset<600?'fade':''}`}>Data Visualization Solution</div>
            <div className={`pj-title ${pjoffset<1400&&pjoffset>1000?'appear':''} ${pjoffset>=1400||pjoffset<1000?'fade':''}`}>Knowledge Graph with D3.js</div>
            <div className={`pj-title ${pjoffset>1400?'appear':''} ${pjoffset<=1400?'fade':''}`}>Business Intelligence Tool</div>
            <div className="pj-pic">
              <img className="pic pic6" style={{top:`${pjoffset<400?-pjoffset*0.3:-120}px`}} src={pic6} alt="" />
              <img className="pic pic7" style={{top:`${pjoffset<400?-pjoffset:-400}px`}}  src={pic7} alt="" />
            </div>
            <div className="pj-pic" style={{zIndex: 10, 
              top:`${pjoffset>=400?(pjoffset<=800?100-(pjoffset-400)/4:0):100}%`,
              height:`${pjoffset>=400?(pjoffset<=800?(pjoffset-400)/4:100):0}%`
              }}>
              {/* <img className="pic pic4" src={pic4} alt="" /> */}
              <img className="pic pic2" src={pic2} alt="" />
            </div>
            <div className="pj-pic" style={{zIndex: 15, 
            top:`${pjoffset>=800?(pjoffset<=1200?100-(pjoffset-800)/4:0):100}%`,
            height:`${pjoffset>=800?(pjoffset<=1200?(pjoffset-800)/4:100):0}%`
          }}>
              <img className="pic pic1" src={pic1} alt="" />
            </div>
            <div className="pj-pic" style={{zIndex: 20, 
            top:`${pjoffset>=1200?(pjoffset<=1600?100-(pjoffset-1200)/4:0):100}%`,
            height:`${pjoffset>=1200?(pjoffset<=1600?(pjoffset-1200)/4:100):0}%`
          }}>
              <img className="pic pic3" src={pic3} alt="" />
              <img className="pic pic5" src={pic5} alt="" />
            </div>
          </div>
        </div>
      </div>
    )
  }
}


